  </section>
</template>
<script>
  export default {
    name: '',
    components: {},
    props: {},
    data () {
      return {}
    },
    watch: {},
    computed: {},
    created () {
      this.init()
    },
    methods: {
      init () {},
      initData () {}
    }
  }
</script>
<style lang="scss">
  .layout {
    background-color: #fff;
    margin: 10px 0;
  }

  .ui-upload {
    text-align: center;
    color: #e5e5e5;
    width: 34.4vw;
    height: 26.4vw;
    border: solid #e5e5e5 1px;
    position: relative;
    overflow: hidden;
    .iconfont {
      line-height: 26.4vw;
      font-size: 30px;
    }
    input[type="file"] {
      width: 100%;
      z-index: 9;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      opacity: 0;
    }
  }
</style>
<style lang="scss" scoped>
</style>



